/* Counter.css */
body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background: linear-gradient(to right, #74ebd5, #ACB6E5);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .counter-container {
    background: #ffffff;
    border-radius: 15px;
    padding: 40px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 350px;
  }
  
  h1 {
    color: #333;
    margin-bottom: 20px;
  }
  
  .counter-value {
    font-size: 48px;
    font-weight: bold;
    color: #1f8efa;
    margin-bottom: 20px;
  }
  
  .input-container {
    margin-bottom: 20px;
  }
  
  .input-container label {
    font-size: 14px;
    color: #555;
  }
  
  .input-field {
    margin-left: 10px;
    padding: 8px;
    width: 50px;
    font-size: 16px;
    text-align: center;
    border: 2px solid #ccc;
    border-radius: 5px;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
  }
  
  .btn {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .increment-btn {
    background-color: #4CAF50;
    color: white;
  }
  
  .increment-btn:hover {
    background-color: #45a049;
  }
  
  .decrement-btn {
    background-color: #f44336;
    color: white;
  }
  
  .decrement-btn:hover {
    background-color: #e53935;
  }
  
  .reset-btn {
    background-color: #555555;
    color: white;
  }
  
  .reset-btn:hover {
    background-color: #444444;
  }
  